import newsItem from '../../components/newsList/newList.vue'
export default{
	data(){
		return{
			newsList:[]
		}
	},
	components:{
		"news-item":newsItem
	},
	onLoad(){
		this.getNewsList()
	},
	methods:{
		//资讯列表
		async getNewsList(){
			const res = await this.$myRequest({
				url:'/api/getnewslist'
			})
			this.newsList = res.data.message
		}
	}
}
